import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Divider, Spin } from 'antd';
import { BtnSave, BtnExport, BtnPrint, BtnAddNew, Btn } from 'components/common/button';
// import { getQueryParams } from 'utils/urlUtil';
import { exportStudentQualifyExcel, exportStudentQualify } from 'services/studentNotQualify';
import FormInfoStudentForExam from './FormInfoStudentForExam';
import TableStudentSelected from './TableStudentSelected';
import TableStudentNotSelected from './TableStudentNotSelected';
import './style.scss';

class ListStuNotEligibleForExam extends Component {
  state = {
    paramsSearch: {},
    listStudentNotSelect: [],
    studentNotSelect: [],
    removeStudent: [],
    listStudentSelect: [],
    removeStudentTableSelect: [],
    saveDataStudent: false,
    listStudentAdd: [],
    loadingPage: false,
    dataTableSelect: [],
  };

  componentDidMount = () => {
    // this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    // this.handleChangeQueryParams();
  };

  // handleChangeQueryParams = () => {
  //   this.setState({ paramsSearch: getQueryParams() });
  // };
  setLoadingSave = value => {
    this.setState({
      loadingPage: value,
    });
  };

  setDataTableSelect = value => {
    this.setState({
      dataTableSelect: value,
    });
  };

  dataForm = value => {
    this.setState({ paramsSearch: value });
  };

  studentSelect = value => {
    this.setState({
      listStudentSelect: value,
    });
  };

  studentNotSelect = value => {
    this.setState({
      listStudentNotSelect: value,
    });
  };

  addStudent = () => {
    const { listStudentNotSelect } = this.state;
    this.setState({
      listStudentNotSelect: [],
      studentNotSelect: listStudentNotSelect,
      removeStudent: listStudentNotSelect,
      listStudentAdd: [],
      removeStudentTableSelect: [],
    });
  };

  deleteStudent = () => {
    const { listStudentSelect } = this.state;
    this.setState({
      listStudentSelect: [],
      removeStudentTableSelect: listStudentSelect,
      listStudentAdd: listStudentSelect,
      removeStudent: [],
      studentNotSelect: [],
    });
  };

  saveListStudent = () => {
    this.setState({ saveDataStudent: true });
  };

  exportExcel = async () => {
    const { paramsSearch } = this.state;
    this.setState({
      loadingPage: true,
    });
    try {
      await exportStudentQualifyExcel(paramsSearch);
    } finally {
      this.setState({
        loadingPage: false,
      });
    }
  };
  exportPdf = async () => {
    const { paramsSearch } = this.state;
    this.setState({
      loadingPage: true,
    });
    try {
      await exportStudentQualify(paramsSearch);
    } finally {
      this.setState({
        loadingPage: false,
      });
    }
  };

  render() {
    const {
      paramsSearch,
      listStudentNotSelect,
      studentNotSelect,
      removeStudent,
      listStudentSelect,
      removeStudentTableSelect,
      saveDataStudent,
      listStudentAdd,
      loadingPage,
      dataTableSelect,
    } = this.state;
    return (
      <Card title="Lập danh sách không đủ điều kiện dự thi">
        <Spin spinning={loadingPage} size="large">
          <Row>
            <Col span={24}>
              <FormInfoStudentForExam dataForm={this.dataForm} />
            </Col>
          </Row>
          <Divider>Danh sách sinh viên chưa chọn</Divider>

          <Row>
            <Col span={24}>
              <TableStudentNotSelected
                paramsSearch={paramsSearch}
                studentNotSelect={this.studentNotSelect}
                removeStudent={removeStudent}
                listStudentAdd={listStudentAdd}
                listStudentNotSelect={listStudentNotSelect}
              />
            </Col>
          </Row>
          <Divider>Danh sách sinh viên đã chọn</Divider>
          <Row>
            <Col span={12}>
              <BtnAddNew disabled={listStudentNotSelect.length === 0} onClick={this.addStudent} />
              <Btn
                style={{ marginLeft: 10 }}
                disabled={listStudentSelect.length === 0}
                title="Xóa"
                onClick={this.deleteStudent}
              />
            </Col>
            <Col span={12} style={{ textAlign: 'end' }}>
              <BtnSave onClick={this.saveListStudent} />
              {dataTableSelect.length > 0 && (
                <Fragment>
                  <BtnExport
                    style={{ marginLeft: 10, marginRight: 10 }}
                    onClick={() => this.exportExcel()}
                  />
                  <BtnPrint title="In" onClick={() => this.exportPdf()} />
                </Fragment>
              )}
            </Col>
          </Row>
          <Row>
            <Col span={24} style={{ textAlign: 'right' }} />
          </Row>
          <Row>
            <Col span={24}>
              <TableStudentSelected
                loading={this.setLoadingSave}
                paramsSearch={paramsSearch}
                saveDataStudent={saveDataStudent}
                studentSelect={this.studentSelect}
                listStudentAdd={studentNotSelect}
                listStudentSelect={listStudentSelect}
                removeStudent={removeStudentTableSelect}
                saveData={() => this.setState({ saveDataStudent: false })}
                dataTableSelect={this.setDataTableSelect}
              />
            </Col>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStuNotEligibleForExam />
  </Page>
);



