import React, { Component } from 'react';
import { Table, message } from 'antd';
import to from 'await-to-js';

import { getStudentQualify, saveStudentQualify } from 'services/studentNotQualify';

class TableStudentChecked extends Component {
  state = {
    selectedRowKeys: [],
    dataTable: {},
  };

  componentWillReceiveProps(nexProps) {
    const { dataTable } = this.state;
    const dataFormat = dataTable;
    if (nexProps.saveDataStudent !== this.props.saveDataStudent) {
      if (nexProps.saveDataStudent) {
        this.saveStudent();
      }
    } else {
      if (nexProps.paramsSearch !== this.props.paramsSearch) {
        this.getTableStudentNotChecked(nexProps.paramsSearch);
      }
      if (nexProps.listStudentAdd.length > 0) {
        nexProps.listStudentAdd.forEach(item => {
          dataFormat[item.studentId] = item;
        });
        this.setState({
          dataTable: dataFormat,
        });
      } else if (nexProps.removeStudent.length > 0) {
        nexProps.removeStudent.forEach(item => {
          delete dataFormat[item.studentId];
        });
        this.setState({
          dataTable: dataFormat,
        });
      }
      if (nexProps.listStudentSelect.length === 0) {
        this.setState({
          selectedRowKeys: [],
        });
      }
    }
  }
  onSelectChange = (selectedRowKeys, rowSelect) => {
    this.setState({ selectedRowKeys });
    this.props.studentSelect(rowSelect);
  };

  getTableStudentNotChecked = async paramsSearch => {
    this.setState({ loading: true });
    const [error, response] = await to(getStudentQualify(paramsSearch));
    this.setState({ loading: false });
    if (error) return;
    const dataTable = {};
    response.data.items.forEach(item => {
      dataTable[item.studentId] = item;
    });
    this.props.dataTableSelect(response.data.items);
    this.setState({
      dataTable,
    });
  };

  saveStudent = async () => {
    const { dataTable } = this.state;
    const dataSave = Object.values(dataTable).map(item => {
      const { id, studentId, subjectId, yearEduId, semesterId, reasonIds, classId } = item;
      const itemFormat = {
        id,
        studentId,
        subjectId,
        yearEduId,
        semesterId,
        reasonIds,
        classId,
      };
      return itemFormat;
    });
    this.props.loading(true);
    const [error, response] = await to(saveStudentQualify(dataSave));
    this.props.saveData();
    this.props.loading(false);
    if (error) return;
    if (response) {
      message.success('Lưu thông tin thành công');
    }
  };

  render() {
    const columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'classname',
        key: 'classname',
      },
      {
        title: 'Tên môn',
        dataIndex: 'subjectname',
        key: 'subjectname',
      },
      {
        title: 'Lý do',
        dataIndex: 'reason',
        key: 'reason',
      },
    ];
    const { dataTable, loading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <Table
        rowSelection={rowSelection}
        loading={loading}
        bordered
        dataSource={Object.values(dataTable)}
        columns={columns}
        rowKey="studentId"
      />
    );
  }
}

export default TableStudentChecked;



