import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { SelectAjax } from 'components/common';
import { getReasons } from 'services/studentNotQualify';
import { BtnClear, BtnSearch } from 'components/common/button';
// import { getListSubject } from 'services/Subject';
// import { updateQueryParams, getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoStudentForExam extends Component {
  state = {};
  // componentDidMount = () => {
  //   const { form } = this.props;
  //   form.setFieldsValue(getQueryParams());
  // };
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) return;
      this.props.dataForm(values);
      // updateQueryParams(values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            span={5}
          />
          {/* <Col span={7}>
            <Form.Item label="Học phần">
              {getFieldDecorator('subjectIds', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Học phần',
                  },
                ],
              })(
                <SelectAjax
                  mode="multiple"
                  // setFieldsValue={setFieldsValue}
                  service={getListSubject}
                  params={{}}
                />
              )}
            </Form.Item>
          </Col> */}
          <Col span={7}>
            <Form.Item label="Lý do">
              {getFieldDecorator('reasonId', {})(
                <SelectAjax
                  // setFieldsValue={setFieldsValue}
                  service={getReasons}
                  params={{}}
                  // allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoStudentForExam;



