import { request, requestDownload } from 'utils';
import qs from 'qs';

const prefix = '/api/education';
export const getReasons = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: '/StudentNotQualify/Reasons',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getStudentNotQualify = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  // subjectIds,
  reasonId,
} = {}) =>
  request({
    prefix,
    url: '/StudentNotQualify/StudentsToEstimate',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      // subjectIds,
      reasonId,
      isPagingEnabled: false,
    },
    paramsSerializer: params => qs.stringify(params, { arrayFormat: 'repeat' }),
    data: {},
  });

export const getStudentQualify = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  // subjectIds,
  reasonId,
} = {}) =>
  request({
    prefix,
    url: '/StudentNotQualify',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      // subjectIds,
      reasonId,
      isPagingEnabled: false,
    },
    paramsSerializer: params => qs.stringify(params, { arrayFormat: 'repeat' }),
    data: {},
  });

export const saveStudentQualify = dataStudent =>
  request({
    prefix,
    url: '/StudentNotQualify',
    method: 'POST',
    data: dataStudent,
  });

export const exportStudentQualifyExcel = paramsSearch =>
  requestDownload({
    defaultFileName: `StudentQualifyExcel.xlsx`,
    prefix,
    url: '/StudentNotQualify/ExportExcelStudentNotQualify',
    method: 'POST',
    data: paramsSearch,
  });

export const exportStudentQualify = paramsSearch =>
  requestDownload({
    defaultFileName: `StudentQualifyExcel.pdf`,
    prefix,
    url: '/StudentNotQualify/ExportStudentNotQualify',
    method: 'POST',
    data: paramsSearch,
  });



